<template>
    <div class="surplus_describe">
      <div class="surplus_describe_head">
        <van-icon name="arrow-left" class="arrow-left pull-left" @click="goBack()"/>
        <span class="title_name"></span>
      </div>
      <!--    -->
      <div class="main_content">
        <h3>Q1: </h3>
        <p></p>
        <h3>Q2: </h3>
        <p>2016122500:00</p>
        <h3>Q3:</h3>
        <p>15050</p>
        <h3>Q4:</h3>
        <p></p>
      </div>
    </div>
</template>

<script>
    export default {
      name: "SurplusDescribe",
      methods:{
        goBack(){
          window.history.go(-1);
        }
      }
    }
</script>

<style scoped>
  /*  */
  .surplus_describe{
    /*animation: fadeInRight 0.2s linear forwards;*/
  }
  .surplus_describe_head{
    background: #3190e8;
    width: 100%;
    height: 4rem;
    text-align: center;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    z-index: 10;
  }
  .arrow-left{
    line-height: 4rem;
    color: white;
    font-size: 2rem;
  }
  .title_name{
    color: white;
    font-size: 2rem;
    text-align: center;
    line-height: 4rem;
  }

  /*  main  */
  .main_content{
    padding: 4rem 1rem 0 1rem;
  }
  h3{
    font-size: 1.7rem;
  }
  p{
    line-height: 2rem;
  }

</style>



