<template>
    <div class="editaddress">
      <van-nav-bar id="one"
        title=""
        left-text=""
        right-text=""
        left-arrow
        @click-left="onClickLeft"
        @click-right="onClickRight"
      />
      <div class="make" v-if="show2" @click="make1"></div>
      <div class="make" v-if="show3" @click="make2"></div>
      <ul>
        <!--,,Vuex,,vuex,Vuex(Vuex),,li,,,,-->
        <li v-for="(k,i) in newaddress">
          <span>{{k.address2}}<br>{{k.phone1}}</span>
          <span class="pull-right" @click="del(i)" v-if="show1">X</span>
        </li>
      </ul>

      <section id="adds" class="per">
        <router-link :to="{path:'/addaddress'}">
          <span style="line-height: 6rem;margin-left: 1rem"></span>
          <span class="pull-right" style="line-height: 6rem;margin-right: 1rem" id="f">&gt
        </span>
        </router-link>
      </section>


    </div>
</template>

<script>
  export default {
    data(){
      return{
        address:"",
        newaddress:"",
        show1:false,
        show2:true,
        show3:false,
      }
    },
    methods: {
      onClickLeft() {
        this.$router.go(-1);
      },
      onClickRight() {
        this.show1 = true;
      },
      del(i){
        console.log(i);
        this.address.splice(i,1);
        console.log(this.address);
        localStorage.setItem("q",JSON.stringify(this.address));
      },
      make1(){
        this.show2 = false;
        this.show3 = true;
        this.show1 = true;
      },
      make2(){
        this.show2 = true;
        this.show3 = false;
        this.show1 = false;
      }
    },
    created(){
      //,addresslocalStorage.getItem("q")
      this.address = JSON.parse(localStorage.getItem("q"));
      console.log(this.address);
      //Vuex,addressVuex
      this.$store.commit("getNewAddress",this.address);
      //newaddresssVuex(VuexlocalStorage)
      this.newaddress = this.$store.state.a.newaddress1;
      //state,
      console.log(this.newaddress);
      let abc = this.$store.state;
      console.log(abc);
      // this.names = this.$store.state.a.message;
    }
  }
</script>

<style scoped>
  .editaddress{
    background: #f5f5f5;
    width: 100%;
    height: 100%;
  }
  #one{
    background: #3190e8;
  }
  .van-nav-bar .van-icon {
    color: white;
    font-size: 2rem;
  }

  .van-nav-bar__title {
    color: white;
    font-weight: 700;
    font-size: 2rem;
  }

  .van-nav-bar__text {
    color: white;
    font-size: 2rem;
  }

  .per{
    width: 100%;
    height: 10%;
    background: white;
    font-size: 2rem;
    border:0.01rem solid #333;
    border-left: none;
    border-right: none;
  }

  a{
    color: black;
    display: inline-block;
    width: 100%;
  }

  #adds{
    margin-top: 1rem;
  }

  li{
    border-bottom: 1px solid #d9d9d9;
    padding: 1rem;
  }
   li:first-child {
    background: #fff8c3;
  }
  .make{
    position: absolute;
    right: 1rem;
    top: 1rem;
    z-index: 99;
    color: white;
    font-size: 2rem;
  }
</style>



