<template>
    <div class="whole">
      <!--    -->
      <div class="first_line">
        <van-icon name="arrow-left" class="arrow-left pull-left" @click="goBack()"/>
        <span class="title_name"></span>
      </div>
      <ul>
        <li class="second_line">
          <span>: </span><span class="admin">1234567890123</span>
        </li>
        <li class="input_border"><input type="text" placeholder="10"></li>
        <li><input type="text" placeholder="6"></li>
        <li><p class="sure_pay"></p></li>
      </ul>
      <footer class="footer">
        <h3></h3>
        <p></p>
        <p><b>30</b></p>
        <p><b>90</b></p>
        <p><b>360</b></p>
        <p class="end_line">3431</p>
      </footer>

    </div>
</template>

<script>
    export default {
      name: "UseCardNumber",
      methods:{
        goBack(){
          window.history.go(-1);
        }
      }
    }
</script>

<style scoped>
  .whole{
    background-color: #f5f5f5;
    height: 100%;
  }
  /*    */
  .first_line{
    line-height: 4rem;
    text-align: center;
    background: #3190e8;
  }
  .arrow-left{
    line-height: 4rem;
    color: white;
    font-size: 2rem;
  }
  .title_name{
    color: white;
    font-size: 2rem;
    font-weight: 600;
    text-align: center;
  }

  .second_line{
    line-height: 4rem;
    margin-left: 6%;
  }
  .admin{
    font-weight: 700;
  }
  /*       */
  input{
    border:0;
    line-height: 4rem;
    padding-left: 6%;
    width: 100%;
    font-size: 1.5rem;
  }
  .input_border{
    border-bottom: 0.05rem solid gainsboro;
  }
  /*      */
  .sure_pay{
    width: 90%;
    color: white;
    background: #4cd964;
    border-radius:0.5rem;
    font-size: 2rem;
    font-weight: 700;
    margin: 3% 5% 0 5%;
    height: 3.5rem;
    line-height: 3.5rem;
    text-align: center;
  }

  /*   footer   */
  .footer{
    margin-left: 10%;
    width: 27rem;
    color: #aaa;
    line-height: 0.8rem;
    font-size: 1rem;
  }
  h3{
    font-size: 1.5rem;
    font-weight: 400;
    text-align: center;
    margin-bottom: 2rem;
  }
  .end_line{
    margin-top: -0.5rem;
    line-height: 1.5rem;
  }
</style>



